<template>
    <div class="preloader">
        <div class="preloader__loader">
        </div>
    </div>
</template>

<script>
export default {
    name: "TwistLoader"
}
</script>

<style scoped>
.preloader {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: white;
    z-index: 1000;
    opacity: .5;
}
.preloader__loader {
    width: 75px;
    height: 75px;
    border: 10px solid black;
    border-radius: 50%;
    border-top-color: blueviolet;
    border-bottom-color: blueviolet;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: 2s spin infinite linear;
}

@keyframes spin {
    from {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}
</style>