<template>
    <div class="card-wrapper">
        <div class="card vote-item">
            <div class="card-body">
                <p class="card-text" style="font-size: 12px;">{{proposal.oracle}}</p>
                <p class="card-text">{{proposal.description}}</p>
            </div>
            <ul class="list-group list-group-flush">
                <li class="list-group-item" v-for="el, index in proposal.names" :key="index">{{proposal.names[index]}}: {{ getType(proposal.metricTypes[index]) }}</li>
            </ul>
            <div class="card-body">
                <p class="card-text">Support: {{proposal.support}}</p>
            </div>
            <div class="card-body">
                <button @click="handleVoteForOracle(proposal.id)" class='btn btn-next btn-fill btn-primary btn-wd' v-if="isVoteTime">Vote</button>
                <button @click="handleFinishOracleVoiting(proposal.id)" class='btn btn-next btn-fill btn-primary btn-wd' v-else>Finish</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
import { typeStringByType } from '@/utils/metricTypes.js';
import { fetchContractData }  from '../utils/web3.js';

export default {
    created() {
        this.date = this.proposal.endTime;
    },
    data() {
        return {
            date: '',
        }
    },
    props: {
        proposal: Object,
    },
    computed: {
        endTime() {
            return new Date(this.date * 1000); 
        },
        isVoteTime() {
            return Date.now() <= this.date * 1000
        }
    },
    methods: {
        ...mapActions(['SEND_DATA','GET_ORACLE_PROPOSALS']),
        getType(type) {
            return typeStringByType[type]
        },
        async handleVoteForOracle(id) {
            const obj = {
                method: 'voteForOracle',
                params: [id]
            }
            const { support } = await fetchContractData('OracleProposals',[id]);
            const supportBefore = support;
            this.SEND_DATA(obj);
            let pingTimer;
            let loopCounter;
            pingTimer = setInterval(async () => {
                const { support } = await fetchContractData('OracleProposals',[id]);
                const supportAfter = support;
                loopCounter++;
                if (supportBefore < supportAfter || loopCounter > 15) {
                    if(supportBefore < supportAfter) {
                        this.$vToastify.success('Data updated successfully', '')
                        await this.GET_ORACLE_PROPOSALS();
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    }
                    clearInterval(pingTimer);
                }
            }, 2000)
        },
        async handleFinishOracleVoiting(id) {
            const obj = {
                method: 'finishOracleVoiting',
                params: [id]
            }
            const lengthBefore = await fetchContractData('getLengthOracleProposals', []);
            this.SEND_DATA(obj);
            let pingTimer;
            let loopCounter;
            pingTimer = setInterval(async () => {
                const lengthAfter = await fetchContractData('getLengthOracleProposals', []);
                loopCounter++;
                if (lengthAfter < lengthBefore || loopCounter > 15) {
                    if(lengthAfter < lengthBefore) {
                        this.$vToastify.success('Data updated successfully', '')
                        await this.GET_ORACLE_PROPOSALS();
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    }
                    clearInterval(pingTimer);
                }
            }, 2000)
        }
    }
}
</script>

<style scoped>
.vote-item {
  background-color: #f3f2ee;
  padding: 5px;
  margin: 5px;
}
.card-wrapper {
    width: 345px;
}
</style>