const { ethers } = require("ethers");


export function getHash(str) {
  return ethers.utils.id(str);
}

export const methodsByType = {
    "1": "getUint",
    "2": "getInt",
    "3": "getFloatUint",
    "4": "getFloatInt",
    "5": "getAddress",
    "6": "getBool",
    "7": "getString",
    "8": "getDate",
    "9": "getBytes32",
}

export const typeStringByType = {
  "1": "Uint",
  "2": "Int",
  "3": "FloatUint",
  "4": "FloatInt",
  "5": "Address",
  "6": "Bool",
  "7": "String",
  "8": "Date",
  "9": "Bytes32",
  "10": "KYC"
}

export function converter(result, type) {
    switch(type) {
        case '3':
        case '4':
          return result.value/(10**result.decimal)
        case '8': 
          return `${result.day}:${result.month}:${result.year} ${result.hour}:${result.minute}`  
        default:
          return result
      }
} 